import { LoadingController, ModalController } from "ionic-angular";
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { JobProcessedSqlViewModel } from "../../Model/ViewModel/JobProcessedSqlViewModel";
import { GlobalComponent } from "../../components/global/global";

/**
 * Generated class for the JobProcessedSqlModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-job-processed-sql-add-edit-modal",
  templateUrl: "job-processed-sql-add-edit-modal.html"
})
export class JobProcessedSqlAddEditModalPage {
  item: any;
  mode: string;
  change_mode: string;
  CanEditJob: boolean;
  title: string;
  Job_type: string;
  sql_help:string=`
  變數說明：
  {ERR_MSG}=異常訊息,
  {ERR_MSG_SEQ}=異常訊息編號,
  {SSIS_JOB_NO}=作業編號,
  {JOBDESC}=作業描述,
  {JOB01},{JOB02},{JOB03}=作業迴圈參數,
   `;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    public global: GlobalComponent,

  ) {
    this.item = new JobProcessedSqlViewModel();
    this.item.ssis_job_no=navParams.data.item.ssis_job_no;//SSIS作業編號
    this.item.exec_status_key=navParams.data.item.exec_status_key;//執行狀態主鍵

    this.item.sql_statement=navParams.data.item.sql_statement;//sql語法
    this.item.is_active=navParams.data.item.is_active;//啟用註記
    this.item.creator=navParams.data.item.creator;//建立者
    this.item.create_time=navParams.data.item.create_time;//建立時間
    this.item.modifier=navParams.data.item.modifier;//最後更新者
    this.item.last_update_time=navParams.data.item.last_update_time;//最後更新時間
    this.item.exec_status=navParams.data.item.exec_status;//執行狀態

    this.CanEditJob = navParams.data.CanEditJob;
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.title = this.item.ssis_job_no;

  }

  SelectExecStatus() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.exec_status_key,
      code_key: "012|C,012|F,012|S",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.exec_status_key = select_data.code_key;
      this.item.exec_status = select_data.code_desc;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  Help() {

    this.global.createLoader();

    this.global.loading.present().then(() => {
       this.global.showMessage("SQL語法說明",this.sql_help);

    });

  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad JobProcessedSqlModalPage");
  }
}



